.wrapper {
    display: inline-flex;
}

.wrapper .static-txt {
    font-size: 60px;
    color: #fff;
    font-weight: 400px;
}

.wrapper .dynamic-txts {
    margin-left: 15px;
    line-height: 85px;
    padding: 2px;
    height: 63px;
    overflow: hidden;
}

.dynamic-txts li {
    list-style: none;
    font-size: 3rem;
    font-weight: 500;
    color: red;
    -webkit-animation: slide 6s steps(4) infinite;
            animation: slide 6s steps(4) infinite;
    position: relative;
    top: 0;
}

.dynamic-txts li span {
    position: relative;
}

.dynamic-txts li span::after {
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    /* background-color: #343F4F; */
    background-color: #0d2343;
    border-left: 3px solid white;
    -webkit-animation: typing 1.5s steps(10) infinite;
            animation: typing 1.5s steps(10) infinite;
}

@-webkit-keyframes typing {
    100% {
        left: 100%;
        margin: 0 -35px 0 35px;
    }
}

@keyframes typing {
    100% {
        left: 100%;
        margin: 0 -35px 0 35px;
    }
}

@-webkit-keyframes slide {
    100% {
        top: -360px;
    }
}

@keyframes slide {
    100% {
        top: -360px;
    }
}
